//   
//   , ,       
//         path     
const sweetConfirmReload = (msg_title, msg_content, path) => {
  return sweet
    .fire({
      customClass: {
        popup: "border",
        confirmButton: "confirmButton",
      },
      width: "auto",
      padding: "0 1rem 1rem",
      title: (
        <Grid>
          <Text size={fontSize.base} bold2="700" margin="0 auto 1rem">
            {msg_title}
          </Text>
          <Text size={fontSize.small}>{msg_content}</Text>
        </Grid>
      ),
      confirmButtonColor: color.brand100,
      confirmButtonText: (
        <Text padding="0 2rem" color={color.bg0}>
          
        </Text>
      ),
      focusConfirm: false,
    })
    .then((res) => {
      if (res.isConfirmed) {
        //    
        if (path === "") {
          return;
        }
        //   history 
        if (path === "history") {
          history.replace("/home");
          return;
        }
        // 
        if (path === "goBack") {
          history.goBack();
          return;
        }
        //     
        window.location.replace(path);
      }
    });
};

