// RoadAddress.js DaumPostCode   
import React from 'react';

import { Header } from '../components';

// style
import { Grid } from '../elements';
import theme from '../styles/theme';

const AddressGrid = (props) => {
  const { border } = theme;

  return (
    <React.Fragment>
      <Grid
        minHeight={props.is_home ? "100vh" : ""}
        minWidth={props.is_post ? '32rem' : '36rem'}
        maxWidth={props.is_post ? '32rem' : '36rem'}
        width={props.is_home ? '36rem' : ''}
        margin="0 auto"
        border={border.line1}
      >
        <Grid shape="container">
          {props.is_post ? (
            <Header {...props} shape="" close={props?.close}>
                
            </Header>
          ) : (
            <Header {...props} shape="" close={props?.close}>
               
            </Header>
          )}
        </Grid>
        {props.children}
      </Grid>
    </React.Fragment>
  );
};

export default AddressGrid;
