import './App.css';
import React from 'react';
import GlobalStyle from './GlobalStyle';

import { Route } from 'react-router-dom';
import { ConnectedRouter } from 'connected-react-router';
import { history } from '../redux/configureStore';
import { useDispatch, useSelector } from 'react-redux';
import { actionCreators as userAction } from '../redux/modules/user';

import Spinner from './Spinner';
import { Grid } from '../elements';

import {
  Main,
  LoginRedirect,
  Tutorial,
  Upload,
  DetailPage,
  Search,
  MyPage,
  RoadAddress,
  ChatRoomList,
  Chat,
  NotFound,
  ProfileEdit,
  Settings,
  MyPost,
  MyReview,
  UserProfile,
  AllowChat,
} from '../pages';

import ReviewWrite from '../pages/ReviewWrite';

//  token 
import { token } from './OAuth';
import logger from './Console';

function App() {
  const dispatch = useDispatch();
  const user_info = useSelector((state) => state.user.user);
  const is_loading = useSelector((state) => state.user.is_loading);
  const is_login = useSelector((state) => state.user.is_login);

  // token   user redux 
  React.useEffect(() => {
    if (token) {
      dispatch(userAction.loginCheck());
      logger('app.js user ', user_info);
    }
    logger('app.js token ', token);
    logger('is_loading', is_loading);
    logger('', is_login);
  }, []);

  if (is_loading) {
    return (
      <React.Fragment>
        <Spinner />
      </React.Fragment>
    );
  }

  return (
    <React.Fragment>
      <Grid>
        <ConnectedRouter history={history}>
          <GlobalStyle />
          <Route path="/" exact component={Tutorial} />
          <Route path="/home" exact component={Main} />
          <Route path="/post/:id" exact component={DetailPage} />
          <Route path="/address" exact component={RoadAddress} />
          <Route path="/user/kakao/callback" exact component={LoginRedirect} />
          <Route path="/upload" exact component={Upload} />
          <Route path="/upload/:id" exact component={Upload} />
          <Route path="/search" component={Search}></Route>
          <Route path="/mypage" component={MyPage}></Route>
          <Route path="/userprofile" component={UserProfile}></Route>
          <Route path="/profile" component={ProfileEdit}></Route>
          <Route path="/mypost" component={MyPost}></Route>
          <Route path="/myreview" component={MyReview}/>
          <Route path="/settings" component={Settings}></Route>
          <Route path="/chatlist" component={ChatRoomList}></Route>
          <Route path="/chatting" component={Chat}></Route>
          <Route path="/allowchat" component={AllowChat}></Route>

          {/* <Route path="*" component={NotFound}></Route> */}
          <Route path="/write" component={ReviewWrite}></Route>
        </ConnectedRouter>
      </Grid>
    </React.Fragment>
  );
}

export default App;
