import React from "react";
import styled from "styled-components";
import { actionCreators as userActions } from "../redux/modules/user";

import { Grid, Button, Text, Input } from "../elements";
import { Header } from "../components";
import theme from "../styles/theme";
import logger from "../shared/Console";
import { useDispatch } from "react-redux";
import { useLocation } from "react-router";

import Select from "../components/ReactSelect";

const { color, border, fontSize } = theme;
// select options
const options = [
  { value: "chocolate", label: "!" },
  { value: "strawberry", label: "~" },
  { value: "vanilla", label: ":(" },
];

const ReviewWrite = (props) => {
  const location = useLocation();
  const dispatch = useDispatch();
  const [manner, setManner] = React.useState("");
  const [review, setReview] = React.useState("");
  const [disabled, setDisabled] = React.useState(true);

  const changeManner = (manner) => {
    if (manner === "!") {
      setManner("BEST");
      setDisabled(false);
    }
    if (manner === "~") {
      setManner("GOOD");
      setDisabled(false);
    }
    if (manner === ":(!") {
      setManner("BAD");
      setDisabled(false);
    }
  };
  const changeDisabled = (e) => {
    setReview(e.target.value);
    setDisabled(false);
  };

  React.useEffect(() => {
    logger("ReviewWrite props: ", props);
    logger("ReviewWrite location-state: ", location.state);
  }, []);

  React.useEffect(() => {
    if (manner === "" && review === "") {
      setDisabled(true);
    } else if (manner && review) {
      setDisabled(false);
    }
  }, [manner, review]);

  return (
    <Grid
      maxWidth="36rem"
      minHeight="100vh"
      border={border.line1}
      margin="0 auto"
    >
      <Grid shape="container">
        <Header {...props} shape="">
           
        </Header>

        <Grid margin="2rem auto 1rem">
          <Profile user_profile={location.state.profile}></Profile>
        </Grid>
        <Grid margin="0 auto 2rem">
          <Text
            width="auto"
            size={fontSize.large}
            color={color.bg100}
            bold2="500"
            line_height="150%"
            text_align="center"
          >
            {location.state.nickname}
          </Text>
        </Grid>
        <GreyLine />
        <Grid padding="0 2rem">
          <Text
            width="auto"
            margin="2rem 0 1rem 0"
            size={fontSize.base}
            color={manner.label ? color.bg100 : color.bg80}
            bold2="500"
            line_height="150%"
          >
               ?
          </Text>
          <Grid margin="0 0 2rem 0">
            <Select
              value={manner}
              options={options}
              changeManner={changeManner}
            ></Select>
          </Grid>
        </Grid>
        <Grid
          width="36rem"
          padding="1rem 3rem 0 3rem"
          borderTop="0.1rem solid #EBE9E8"
        >
          <Input
            bold="400"
            border="none"
            size={fontSize.base}
            placeholder="      !"
            multiLine="t"
            length="300"
            color="#9A9896"
            value={review}
            _onChange={changeDisabled}
          />
        </Grid>

        {/* <Grid margin="1.5rem 0" padding="0 2rem">
          <Text
            width="auto"
            size={fontSize.small}
            color={manner.label ? color.bg100 : color.bg80}
            bold2="400"
            line_height="150%"
            text_align="left"
          >
            {manner.label
              ? " !    ~"
              : "      !"}
          </Text>
        </Grid> */}
      </Grid>
      <Grid
        maxWidth="35.9rem"
        width="100%"
        height="auto"
        margin="1rem auto 0 auto"
        padding="1.5rem 2rem"
        is_fixed="t"
        borderTop="0.1rem solid #EBE9E8"
      >
        <Button
          shape="large"
          color={!disabled ? color.brand100 : color.bg40}
          size={fontSize.small}
          cursor="t"
          _onClick={() => {
            window.alert(" ?");
            dispatch(
              userActions.reviewWriteAX(manner, review, location.state?.user_id)
            );
          }}
          disabled={disabled}
        >
          <Text bold size="1.6rem" color={!disabled ? color.bg0 : color.bg60}>
            
          </Text>
        </Button>
      </Grid>
    </Grid>
  );
};

ReviewWrite.defaultProps = {};

const Profile = styled.div`
  margin: auto;
  width: 5rem;
  height: 5rem;
  border-radius: 2.5rem;
  ${(props) =>
    props.user_profile
      ? `background-image: url(${props.user_profile});`
      : `background-image: url(http://115.85.182.57:8080/image/profileDefaultImg.jpg);`}
  background-size: cover;
  background-position: center;
`;

const GreyLine = styled.div`
  box-sizing: border-box;
  width: 100%;
  height: 1px;
  margin: 1rem auto;
  background-color: #f1f2f4;
  &:hover {
    background-color: red;
  }
`;

export default ReviewWrite;
