import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { actionCreators as chatActions } from "../redux/modules/chat";
import { history } from "../redux/configureStore";

// style
import { Header, Footer, ChatListItem, AwaitList } from "../components";
import { Button, Grid, Input, Text } from "../elements";
import theme from "../styles/theme";
import styled from "styled-components";

import logger from "../shared/Console";

const ChatRoomList = (props) => {
  const dispatch = useDispatch();
  React.useEffect(() => {
    dispatch(chatActions.setChatListAX());
    dispatch(chatActions.awaitChatListAX());
  }, []);

  //  room_id 

  const { color, border, radius, fontSize } = theme;

  //  
  const chat_list = useSelector((state) => state.chat.chatListInfo);

  //   
  const await_list = useSelector((state) => state.chat.awaitList);

  const enterRoom = (room_id, roomName, post_id) => {
    //      
    // room_id           

    //      axios 
    //  
    // /chat/join/${room_id}
    dispatch(chatActions.clearMessage());
    dispatch(chatActions.moveChatRoom(room_id, roomName, post_id));
    history.replace({
      pathname: "/chatting",
      state: { room_id: room_id, roomName: roomName, post_id: post_id },
    });
  };

  return (
    <React.Fragment>
      <Grid
        maxWidth="36rem"
        minHeight="100vh"
        border={border.line1}
        margin="0 auto"
        padding="0 0 6rem 0"
      >
        <Grid shape="container">
          <Header {...props} shape="" />

          <Grid is_flex4="t" text_align="center" borderBottom={border.line1}>
            <Grid
              borderBottom={border.line3}
              width="auto"
              padding="0 2rem"
              margin="auto"
              _onClick={() => {
                history.push("/chatlist");
              }}
              cursor="pointer"
            >
              <Text size={fontSize.base} bold2="700" margin="0 0 0.8rem">
                 
              </Text>
            </Grid>
            <Grid width="auto" padding="0 2rem" margin="auto">
              <Text
                bold2="700"
                size={fontSize.base}
                margin="0 0 0.8rem"
                color={color.bg60}
                _onClick={() => {
                  history.push("/allowchat");
                }}
                cursor="pointer"
              >
                  
              </Text>
            </Grid>
          </Grid>
          {chat_list.map((info, idx) => {
            return (
              <ChatListItem
                key={idx}
                room_id={info.room_id}
                roomName={info.roomName}
                post_id={info.postId}
                headCountChat={info.headCountChat}
                _onClick={(e) => {
                  enterRoom(info.room_id, info.roomName, info.postId);
                }}
              />
            );
          })}

          {await_list.map((info, idx) => {
            return <AwaitList key={idx} roomName={info.title} />;
          })}

          {await_list.length === 0 && chat_list.length === 0 && (
            <>
              <Grid shape="empty" src="illust/emptyBubbles.png">
                <EmptyText theme={theme}>   .</EmptyText>
              </Grid>
              <Grid height="20rem"></Grid>
            </>
          )}

          <Footer {...props}></Footer>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

ChatRoomList.defaultProps = {};

const EmptyText = styled.p`
  position: relative;
  text-align: center;
  top: 15rem;
  font-size: ${(props) => props.theme.fontSize.base};
  color: ${(props) => props.theme.color.bg80};
`;

export default ChatRoomList;
